import { defineStore } from 'pinia'

export const useMainStore = defineStore('main', {
    state: () => ({
        // darkTheme,
        theme: null,
    }),
    // optional getters
    getters: {

    },
    // optional actions
    actions: {
        // 改变主题
        changeTheme(e) {
            this.theme = e;
        },
    },
})

export default useMainStore