<template>
	<n-config-provider :theme="theme" :locale="zhCN" :date-locale="dateZhCN" >
		<n-card :bordered="false" content-style="padding: 0;">
		<div id="app" class="font-family: @themeFontFamily">
			<div class=""></div>
			<div class="body">
				<router-view v-slot="{ Component, route }">
					<transition :name="route.meta.transition || 'fade'" mode="out-in">
						<keep-alive>
							<div>
								<component :is="Component" :key="route.meta.usePathKey ? route.path : undefined" />
							</div>
						</keep-alive>
					</transition>
				</router-view>
			</div>
		</div>
		</n-card>
	</n-config-provider>
</template>

<script>
import { zhCN, dateZhCN, darkTheme } from 'naive-ui';
import { useMainStore } from '@/store';
import { storeToRefs } from 'pinia';
export default {
	name: "App",
	components: {
		
	},
	data() {
		return {
			zhCN,
        	dateZhCN,
			active: '',
			darkTheme,
			theme: null,
			nav: [
				{name: 'home', router: '/'},
			],
		}
	},
	created() {

	},
	mounted() {
		// 初始化nav-tab位置
		this.initNavTab();
		// 页面好了再加载主题
		this.theme = storeToRefs(useMainStore()).theme;
	},
	methods: {
		// ----------------------------------------- init -----------------------------------------
		// 初始化nav-tab位置 （这路由mounted好了还要等会才能拿到能不能行了老铁）
		initNavTab () {
			setTimeout(() => {
				let hasRouter = false;
				this.nav.forEach((item, index) => {
					if (item.router === this.$route.path) {
						this.active = index;
						hasRouter = true
					}
				})
				if (!hasRouter) this.active = ''
			}, 500);
		},
		// ----------------------------------------- onPress -----------------------------------------
		// 点击nav的路由操作
		onPressRouter(router, index, link="") {
			// wwelcome页面不需要navtab跟随
			if (index === undefined) {
				this.active = '';
			} else {
				this.active = index;
			}
            let url = window.location.href;
            if (router === 'web') {
                window.location.href = url+link;
            } else {
                this.$router.push({path: router});
            }
        },
	}
};
</script>

<style lang="less">
@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";
* {
	margin: 0;
	padding: 0;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	::-webkit-scrollbar {
		width: 0;
	};
}
body {
	overflow: hidden;
}
input {
	border: 0;
    outline: none; // 去除选中状态边框
    background-color: rgba(0, 0, 0, 0);// 透明背景
}
img {
	border: 0;
}
#app {
	width: 100vw;
	height: 100vh;
	// background: url("@/assets/img/bg.png") no-repeat fixed center;
    background-size: cover;
	display: flex;
	flex-direction: column;
	overflow: hidden;

	> .header {

    }

	> .body {
		flex: 1;
		width: 100%;
		overflow: hidden;

		> div {
			width: 100%;
			height: 100%;
		}
	}
}

.themeColor {
	background: var(--themeColor);
}
.themeColorBg {
	background: var(--themeColorBg);
}

.n-card {
	padding-top: 0px !important;
}
</style>
